import React from 'react';
import PageTemplate from 'components/common/PageTemplate';
import FormContainer from 'containers/Form/FormContainer';

const FormPage = ({match}) => {
    if(match.path === "/login") {
        return (
            <PageTemplate>
                <FormContainer title=""/>
            </PageTemplate>
        );
    }
    if(match.path === "/register") {
        return (
            <PageTemplate>
                <FormContainer title=""/>
            </PageTemplate>
        );
    }
    if(match.path === "/admin") {
        return (
            <PageTemplate>
                <FormContainer title=" "/>
            </PageTemplate>
        );
    }
    
    
};

export default FormPage;


