import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import AuthWrapper from 'components/auth/AuthWrapper';
import AuthContent from 'components/auth/AuthContent';
import InputWithLabel from 'components/auth/InputWithLabel';
import Button from 'components/common/Button';
import * as authActions from 'store/modules/auth';
import * as baseActions from 'store/modules/base';
import { withRouter } from 'react-router-dom';
import storage from 'lib/storage';
import InputPhoneWrapper from 'components/auth/InputPhoneWrapper/InputPhoneWrapper';
import InputWithLabelPhone from 'components/auth/InputWithLabelPhone/InputWithLabelPhone';

if (typeof window === 'undefined') {
    global.window = {}
}
const $ = window.$;
const daum = window.daum;

class FormContainer extends Component {

    initialize = () => {
        const { AuthActions } = this.props;
        AuthActions.initialize();

    }

    componentDidMount() {
        // if(localStorage.adminLogged === "true" || localStorage.memberLogged === "true") {
        //     alert("  .");
        //     document.location.href = "/";
        // }
        this.initialize();
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.title !== this.props.title) {
            this.initialize();
        }
    }



    handleChangeInput = (e) => {
        const { name, value } = e.target;
        const { AuthActions } = this.props;
        AuthActions.changeInput({ name, value });
    }



    handleKeyPress = (e) => {
        if (e.key === "Enter") {
            this.adminLogin();
        }
    }

    handleRegister = async () => {
        const { AuthActions, history } = this.props;
        const { userID,
            userEmail,
            userPassword,
            userPasswordConfirm,
            userName, userAddress,
            userPostCode,
            userDetailAddress,
            userPhonePost, userPhoneCenter, userPhoneRear } = this.props.input.toJS();

        if (userID === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userPassword === "" || userPasswordConfirm === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userPassword !== userPasswordConfirm) {
            AuthActions.setErrorMessage({ errorMessage: "   " });
            return;
        }
        if (userName === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userEmail === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userAddress === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userPostCode === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        if (userDetailAddress === "") {
            AuthActions.setErrorMessage({ errorMessage: "  " });
            return;
        }
        if(userPhonePost === "" || userPhoneCenter === "" || userPhoneRear === "") {
            AuthActions.setErrorMessage({ errorMessage: " " });
            return;
        }
        const config = {
            headers: { Pragma: 'no-cache' }
        };
        try {
            await AuthActions.register({ userID, userPassword, userEmail, userName, userAddress, userPostCode, userDetailAddress, userPhone: `${userPhonePost}-${userPhoneCenter}-${userPhoneRear}` }, config);
            this.initialize();
            alert(" !   .");
            history.push('/login');
        } catch (e) {
            console.log(e);
        }
    }

    handleKeyPressRegister = (e) => {
        if (e.key === "Enter") {
            this.handleRegister();
        }
    }

    handleMemberLogin = async () => {
        const { AuthActions, history } = this.props;
        const { userID, userPassword } = this.props.input.toJS();

        const config = {
            headers: { Pragma: 'no-cache' }
        };
        try {
            await AuthActions.login({ userID, userPassword }, config);
            // console.log(this.props.memberLoggedResult);
            const loggedInfo = this.props.memberLoggedResult;
            storage.set('loggedInfo', loggedInfo);
            localStorage.memberLogged = "true";
            AuthActions.setLoggedInfo(loggedInfo);
            history.push("/");
        } catch (e) {
            console.log(e);
        }
    }

    adminLogin = async () => {
        const { BaseActions, history } = this.props;
        const { userID, userPassword } = this.props.input.toJS();
        const config = {
            headers: { Pragma: 'no-cache' }
        };

        try {
            await BaseActions.adminLogin({ adminID: userID, password: userPassword }, config);
            storage.set('adminLogged', true);
            this.initialize();
            history.push('/admin/list');
        } catch (e) {
            console.log(e);
        }
    }

    handleKeyPressMemberLogin = (e) => {
        if (e.key === "Enter") {
            this.handleMemberLogin();
        }
    }

    // componentDidUpdate(prevProps, prevState) {
    //     const { AuthActions } = this.props;
    //     if(prevProps.errorMessage !== this.props.errorMessage && this.props.errorMessage !== "") {
    //         alert(this.props.errorMessage);
    //         AuthActions.resetErrorMessage();
    //     }
    // }

    handleAddressMapOpen = () => {

        const { AuthActions } = this.props;
        new daum.Postcode({
            oncomplete: function (data) {
                //        .       .    
                //  ('') ,    .
                var fullAddr = ''; //   
                var extraAddr = ''; //   

                //         .
                if (data.userSelectedType === 'R') { //     
                    fullAddr = data.roadAddress;

                } else { //     (J)
                    fullAddr = data.jibunAddress;
                }

                //      .
                if (data.userSelectedType === 'R') {
                    //   .
                    if (data.bname !== '') {
                        extraAddr += data.bname;
                    }
                    //    .
                    if (data.buildingName !== '') {
                        extraAddr += (extraAddr !== ''
                            ? ', ' + data.buildingName
                            : data.buildingName);
                    }
                    //         .
                    fullAddr += (extraAddr !== ''
                        ? ' (' + extraAddr + ')'
                        : '');
                }

                //      .
                $('input[id=buyer_postcode]').val(data.zonecode);
                $('input[id=buyer_addr]').val(fullAddr);
                AuthActions.changeInput({ name: 'userPostCode', value: data.zonecode });
                AuthActions.changeInput({ name: 'userAddress', value: fullAddr });

                //    .
                $('input[id=detail_addr]').focus();
            }
        }).open();
    }



    render() {
        const { title } = this.props;
        const { handleChangeInput,
            adminLogin,
            handleKeyPress,
            handleRegister,
            handleKeyPressRegister,
            handleMemberLogin,
            handleKeyPressMemberLogin,
            handleAddressMapOpen } = this;

        const { userID,
            userPassword,
            userPasswordConfirm,
            userEmail,
            userName,
            userPhonePost,
            userPhoneCenter,
            userPhoneRear,
            userAddress,
            userPostCode,
            userDetailAddress } = this.props.input.toJS();
        // const title = ""; if(category === "/login") {     title = ""; } else {
        // title = ""; }
        if (title === "") {
            const { memberLogged, errorFlashVisible, errorMessage } = this.props;
            if (memberLogged) {

                return null;
            }
            return (
                <AuthWrapper>
                    <AuthContent mode="login" title={title} onButtonClick={handleMemberLogin} buttonText={title} visible={errorFlashVisible} errorMessage={errorMessage}>
                        <InputWithLabel onChange={handleChangeInput} label="" value={userID} name="userID" placeholder="" />
                        <InputWithLabel onChange={handleChangeInput} type="password" label="" value={userPassword} name="userPassword" placeholder="" onKeyPress={handleKeyPressMemberLogin} />
                    </AuthContent>
                </AuthWrapper>
            );
        }
        if (title === "") {
            const { memberLogged, errorFlashVisible, errorMessage } = this.props;
            if (memberLogged) {
                return null;
            }
            return (
                <AuthWrapper>
                    <AuthContent title={title} onButtonClick={handleRegister} buttonText={title} visible={errorFlashVisible} errorMessage={errorMessage}>
                        <InputWithLabel onChange={handleChangeInput} label="" value={userID} name="userID" placeholder="" />
                        <InputWithLabel onChange={handleChangeInput} type="password" label="" value={userPassword} name="userPassword" placeholder="" />
                        <InputWithLabel
                            onChange={handleChangeInput}
                            label=" "
                            value={userPasswordConfirm}
                            type="password"
                            name="userPasswordConfirm"
                            placeholder=" " />
                        <InputWithLabel onChange={handleChangeInput} label="" value={userName} name="userName" placeholder="" onKeyPress={handleKeyPressRegister} />
                        <InputWithLabel onChange={handleChangeInput} label="" value={userEmail} name="userEmail" placeholder="" />
                        <InputPhoneWrapper label=" ">
                            <InputWithLabelPhone
                                value={userPhonePost}
                                onChange={handleChangeInput}
                                name="userPhonePost"
                                placeholder=" " />
                            <InputWithLabelPhone
                                value={userPhoneCenter}
                                onChange={handleChangeInput}
                                name="userPhoneCenter"
                                placeholder=" " />
                            <InputWithLabelPhone
                                value={userPhoneRear}
                                onChange={handleChangeInput}
                                name="userPhoneRear"
                                placeholder=" " />
                        </InputPhoneWrapper>
                        <Button theme="address" onClick={handleAddressMapOpen}>  </Button>
                        <InputWithLabel
                            onChange={handleChangeInput}
                            label=""
                            value={userAddress}
                            name="userAddress"
                            placeholder=""
                            disabled={true}
                            id="buyer_addr"
                        ></InputWithLabel>
                        <InputWithLabel
                            onChange={handleChangeInput}
                            label=""
                            value={userPostCode}
                            name="userPostCode"
                            id="buyer_postcode"
                            disabled={true}
                            placeholder=""></InputWithLabel>
                        <InputWithLabel
                            onChange={handleChangeInput}
                            label=" "
                            value={userDetailAddress}
                            name="userDetailAddress"
                            id="detail_addr"
                            placeholder="  "></InputWithLabel>
                    </AuthContent>
                </AuthWrapper>
            );
        }
        if (title === " ") {
            const { adminLogged, errorVisible, errorMessageBase } = this.props;
            if (adminLogged) {
                return null;
            }
            return (
                <AuthWrapper>
                    <AuthContent visible={errorVisible} errorMessage={errorMessageBase} title={title} onButtonClick={adminLogin} buttonText={title}>
                        <InputWithLabel onChange={handleChangeInput} label="" value={userID} name="userID" placeholder="" />
                        <InputWithLabel onKeyPress={handleKeyPress} onChange={handleChangeInput} type="password" label="" value={userPassword} name="userPassword" placeholder="" />
                    </AuthContent>
                </AuthWrapper>
            );
        }

    }
}

export default connect((state) => ({
    input: state.auth.get('input'),
    adminLogged: state.base.get('adminLogged'),
    errorMessage: state.auth.get('errorMessage'),
    memberLogged: state.base.get('memberLogged'),
    errorFlashVisible: state.auth.get('errorFlashVisible'),
    errorVisible: state.base.get('errorVisible'),
    errorMessageBase: state.base.get('errorMessage'),
    memberLoggedResult: state.auth.get('memberLoggedResult')
}), (dispatch) => ({
    AuthActions: bindActionCreators(authActions, dispatch),
    BaseActions: bindActionCreators(baseActions, dispatch)
}))(withRouter(FormContainer));


